import React, { Component } from 'react';
import { MdDeveloperMode } from 'react-icons/md';
import { Link } from 'react-router-dom';

export default class Header extends Component {
    render() {
        return (
            <div className="row justify-content-center px-4 py-5">
                <div className="col-md-4 col-12">
                    <h3><MdDeveloperMode className="mb-1" /> <Link to="/" className="link">Praphul Patil</Link></h3>
                </div>                
                <div className="col-md-7 col-12 text-end">
                    {this.props.showMenu ? 
                        <ul className="header-menu">
                            <li><Link to="/resume" className="link">Resume</Link></li>
                            <li><Link to="/projects" className="link">Projects</Link></li>
                        </ul>                        
                    : ''}
                </div>
            </div>            
        )
    }
}
