import React, { Component } from 'react';
import { FaFacebookSquare } from 'react-icons/fa';
import { FaLinkedin } from 'react-icons/fa';

export default class Footer extends Component {
    render() {
        return (
            <footer>
                <div className="row justify-content-center footer-container mt-5 py-3 px-4 border-top">
                    <div className="col-md-3 col-12 mt-3">
                        <div className="label">Phone</div>
                        <div className="label-value">+91-9589889609</div>
                    </div>
                    <div className="col-md-4 col-12 mt-3">
                        <div className="label">Email</div>
                        <div className="label-value">praphulpatil96@gmail.com</div>
                    </div>
                    <div className="col-md-3 col-12 mt-3">
                        <div className="label">Follow Me</div>
                        <div className="label-value label-link">
                            <FaFacebookSquare onClick={() => window.open('https://www.facebook.com/praphul.patil.75')}/>&nbsp;
                            <FaLinkedin onClick={() => window.open('https://www.linkedin.com/in/praphul-patil-aba896157/')}/></div>
                    </div>
                    <div className="col-md-2 col-12 mt-3">
                        <div>&#169; 2021 By Praphul Patil</div>
                    </div>
                </div>                
            </footer>
        )
    }
}
