import React, { Component } from 'react'
import { Link } from 'react-router-dom'
import {StyleRoot} from 'radium';
import { fadeInEffect } from './Constants';
import ScrollToTop from './ScrollToTop';
import pdf from '../resume.pdf';
import { MdFileDownload } from 'react-icons/md';

export default class Home extends Component {
    render() {
        return (
            <StyleRoot>
                <ScrollToTop />
                <div className="row body-container justify-content-center align-items-center px-5" style={fadeInEffect()}>
                    <div className="col-12 col-md-3 bg-secondary photo-box"></div>
                    <div className="col-12 col-md-3 intro-box px-5 pt-2">                        
                        <div className="row justify-content-center">
                            <span id="heading"><b>Hello</b></span>
                            <p id="sub-heading"><b>Bit about me</b></p>
                            <p id="description">Enthusiastic <b>Senior Software Engineer</b> with 3+ years of experience in back-end as well as on front-end and participated in the complete product development lifecycle of successfully launched applications.</p>
                            <div className="col-4">
                                <Link to="/resume" className="link"><button className="circular-btn btn-primary shadow">Resume</button></Link>
                            </div>
                            <div className="col-4">
                                <Link to="/projects" className="link"><button className="circular-btn btn-danger shadow">Projects</button></Link>
                            </div>
                            <div className="col-4">
                                <a href={pdf} name="resume" download="PRAPHUL_PATIL (Senior Software Engineer)"><button className="circular-btn btn-success shadow">&nbsp;PDF<MdFileDownload /></button></a>
                            </div>
                        </div>
                    </div>
                </div>
            </StyleRoot>
        )
    }
}
