import React, { Component } from 'react';
import {
    Switch,
    Route
  } from "react-router-dom";
import Home from './Home';
import Projects from './Projects';
import Resume from './Resume';

export default class PageRoute extends Component {
    render() {
        return (
            <Switch>
                <Route onUpdate={() => window.scrollTo(0, 0)} path="/" component={Home} exact />
                <Route onUpdate={() => window.scrollTo(0, 0)} path="/resume" component={Resume} />
                <Route onUpdate={() => window.scrollTo(0, 0)} path="/projects" component={Projects} />
            </Switch>
        )
    }
}
